<template>
	<div class="customerService">
		<div class="customerService_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="customerService_title">联系客服</p>
		</div>
		<div class="customerService_main">
			<img :src="customerServiceUrl" class="icon_code" />
			<p class="text1">请添加客服微信</p>
			<p class="text2">客服工作时间：每周一至周五10:00-19:00</p>
		</div>
		
	</div>
</template>

<script>
	export default{
		name:'customerService',
		data(){
			return {
				customerServiceUrl:''
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.getMediaFreeReceiveConfig();
		},
		methods:{
			getMediaFreeReceiveConfig(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/freebase/getMediaFreeReceiveConfig',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.customerServiceUrl = reData.customerServiceUrl;
					}
				})
			},
			goBack(){
				this.$router.go(-1);
			},
		}
	}
</script>

<style scoped>
	.customerService{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fff;
		padding-bottom:0.8rem;
	}
	.customerService_header{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.3rem;
		padding-right:0.3rem;
		position: relative;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../static/img/icon_arrow_left.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
	  z-index: 4;
	}
	.customerService_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		z-index: 3;
	}
	.customerService_main{
		width: 100%;
		text-align: center;
		color:#000;
		padding-top:1.8rem;
	}
	.customerService_main .icon_code{
		width: 4.1rem;
		height: 4.1rem;
		margin:0 auto;
	}
	.customerService_main .text1{
		font-size: 0.48rem;
		margin-top:0.7rem;
	}
	.customerService_main .text2{
		font-size: 0.36rem;
		margin-top:0.1rem;
	}
</style>
